.App {
  text-align: center;
  font-family: 'KoHo', sans-serif;
}

/*^^^^^^^^^^^ FORM ^^^^^^^^^^^^^^^^^^^^^^*/

.form-wrap {
  height: 90vh;
  width: 90vw;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #dad8dc;
  border: 1px solid #ebebeb;
  box-shadow: 5px 10px 8px #888888;
}

.search-form-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  background-color: #cfdad4;
  border-bottom: 2px solid white;
  width: 100vw;
  height: 100vh;
}

label {
  margin: 5%;
}


/*^^^^^^^^^^ HEADER ^^^^^^^^^^^^^^^^^^^^*/

header {
  background-color: #29465d;
  color:  #ebebeb;
  height: 15vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

header h1 {
  display: none;
}

nav {
  display: flex;
  justify-content: space-around;
  padding: 0;
  padding-left: 3%;
  padding-right: 3%;
}

a{
  background-color: #ebebeb;
  color: #29465d;
  text-decoration: none;
  padding: 9px;
  margin: 5px;
  border-radius: 5px;
  height: 10%;
  align-self: flex-end;
}

/*^^^^^^^^^^ MAIN CONTENT/PET CARD ^^^^^^^^^^^^*/

.main-content-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
}

.pet-card-container {
  margin: 5%;
}

.pet-card {
  display: flex;
  height: 260px;
  width: 250px;
  align-items: center;
  justify-content: flex-start;
  background: #dad8dc;
  border-radius: 20px;
  flex-direction: column;
  box-shadow: 5px 10px 8px #888888;
}

.card-img {
  height: 125px;
  width: 125px;
}

img {
  height: 100%;
  width: 100%;
}

.pet-name-header {
  width: 100%;
  background-color: #193242;
  color:  #ebebeb;
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;  
}

/*^^^^^^^^^^^ FEATURED PETS ^^^^^^^^^^^^^^^^^^^^^^*/

.feat-pet-content-wrap {
  flex-direction: column;
  justify-content: space-around;
}

.feat-pet-banner h2 {
    position: relative;
    bottom: -60px;
}

.feat-pet-card-wrap {
  height: 185px;
  background: #193242;
  display: flex;
  justify-content: flex-end;
  box-shadow: 5px 10px 8px #888888;
}

.cat-card,
.dog-card {
  width: 40vw;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.fp-name-header {
  font-size: 10px;
  font-weight: bold;
  color: #ebebeb
}


/*^^^^^^^^^^^^^^ NO-PET-AVAILABLE ^^^^^^^^^^^^^^^^^^^^*/

.empty-card-img {
  width: 300px;
}

.empty-card-info {
  flex-direction: column;
}


/*^^^^^^^^^^^^^^ MEDIA QUERIES ^^^^^^^^^^^^^^^^^^^^*/

@media (max-width: 325px) {
  .cat-card,
  .dog-card {
    height: auto;
  }
}

@media (min-width: 767px) {
  /*.App {
    font-size: 15px;
  }*/

  .button-wrap button {
    padding: 15px;
  }

  header {
    flex-direction: row;
    justify-content: space-between;
  }

  header h1 {
    display: flex;
    padding-left: 3%;
    margin: 0;
    align-self: center;
  }

  .welcome h1 {
    font-size: 60px;
  }

  .feat-pet-banner h2 {
    font-size: 45px;
  }

  .feat-pet-card-wrap {
    width: 70vw;
    height: 30vh;
  }

  .card-img {
    height: 200px;
    width: 200px;
  }

  .empty-card-img {
    width: 500px;
  }
}

@media (min-width: 850px) {
  .feat-pet-card-wrap {
    height: 50vh;
  }

  .pet-card {
  height: 350px;
  width: 300px;
  }

  .feat-pet-banner h2 {
    position: relative;
    bottom: -40px;
  }
}
